@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.header {
  margin-block: 50px 100px;
  width: 100%;
  text-align: center;
}

.card {
  @include f.flexLayout(center, center, column, 20px);
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  height: 250px;
  border-radius: 7px;
  padding: 0.25rem 0.5rem;
  h3 {
    font-size: 18px;
    font-weight: 600;
    text-align: center;
  }
  button {
    background-color: #001736;
    color: rgb(255, 255, 255);
    padding: 13px 20px;
    text-align: center;
    border-radius: 5px;
    &:hover {
      transform: scale(1.1);
      transition: all ease-in-out 0.3s;
    }
  }
}
