@mixin flexLayout($justifyContent, $alignItems, $directions, $gap) {
  display: flex;
  justify-content: $justifyContent;
  align-items: $alignItems;
  flex-direction: $directions;
  gap: $gap;
}

@mixin rtl($property, $ltr-value, $rtl-value) {
  #{$property}: $rtl-value;

  [dir="rtl"] & {
    #{$property}: $ltr-value;
  }
}

@mixin fontStyles($lineHeight, $fontSize, $fontWeight, $color, $fontFamily) {
  line-height: $lineHeight;
  font-size: $fontSize;
  color: $color;
  font-weight: $fontWeight;
  font-family: $fontFamily;
}

@mixin paddingY($value) {
  padding-top: $value;
  padding-bottom: $value;
}

@mixin scrollBars(
  $size,
  $foreground-color,
  $foregroundOnHover-color,
  $background-color: mix($foreground-color, white, 50%)
) {
  // For Google Chrome
  &::-webkit-scrollbar {
    width: $size;
    height: $size;
  }

  &::-webkit-scrollbar-thumb {
    background: $foreground-color;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: $foregroundOnHover-color;
  }

  &::-webkit-scrollbar-track {
    background: $background-color;
  }

  // For Internet Explorer
  & {
    scrollbar-face-color: $foreground-color;
    scrollbar-track-color: $background-color;
  }
}

@mixin xs {
  @media (min-width: 320px) {
    @content;
  }
}

@mixin sm {
  @media (min-width: 576px) {
    @content;
  }
}
@mixin md {
  @media (min-width: 768px) {
    @content;
  }
}
@mixin lg {
  @media (min-width: 992px) {
    @content;
  }
}
@mixin xl {
  @media (min-width: 1200px) {
    @content;
  }
}
@mixin xxl {
  @media (min-width: 1400px) {
    @content;
  }
}
